<template>
    <!-- 新增发布求职 -->
    <div class="job">
        <navigate title="发布求职" position="absolute">
            <mu-button slot="right" color="#fff" @click="handlerSubmit" flat>发送</mu-button>
        </navigate>
        <div class="scroll-wrapper">
            <!-- 输入框 -->
            <mu-text-field v-model="content" placeholder="个人信息" style="padding: 10px 15px;"
                           :rows="20"
                           multi-line
                           full-width
                           solo/>
            <!-- 输入框 -->
        </div>
    </div>
    <!-- 新增发布求职 -->
</template>

<script>
    import Navigate from "../../base/navigate/navigate"
    import {saveMoment} from "../../api/dynamic"

    export default {
        name: "job",
        data() {
            return {
                content: ""
            }
        },
        activated() {
            this.content = ''
        },
        methods: {
            async handlerSubmit() {
                const loading = this.$loading()

                if (!this.content) {
                    this.$toast('请填写个人信息!')

                    return loading.close()
                }

                const data = {
                    type: '004004',
                    mediaType: '003002',
                    status: '020002',
                    content: this.content
                }

                await saveMoment(data)

                loading.close()

                this.$toast('发表成功！')

                this.$emit('refresh')

                this.$router.go(-2)
            }
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    .job
        padding-top 46px
</style>



